import React from 'react';

import './style/StartView.css';
export default class StartView extends React.Component{
  constructor(props){
    super(props);
    this.state = {playerName: this.props.playerName}
  }
  render(){
    const caution_text = `\
    最終更新 2020/03/15
    
    [注意]
    ☆ 現在作成中につき一部機能は正しく動作していません
    ☆ 個人製作品のため品質等保証はしません
    ☆ 現在実装済み機能
    　　・走る
    　　・座らせる
    　　・アイテム画面表示
    　　・撫でる
    　　・プレイヤー名設定
    　　　(途中変更、自己紹介編集はできません)
    ☆ わかりづらい未実装
    　　・レベルアップ
    　　　(複数回撫でると簡易的にレベルアップします)
    　　・Windowサイズ途中変更(再読み込みしてください)
    `
    return(
      <div className="start-element">
      <div className="game-title">ゆうせいくん 育成ゲーム (仮)</div>
      <div className="player-name-input">
        <form>
          <label>飼い主:</label>
          <input type="text" value={this.state.playerName} onChange={(e)=>{
            this.setState({playerName: e.target.value})
            this.props.setPlayerName(e)}}/>          
        </form>
      </div>
      <button onClick={()=>{
        this.props.startClick()
       }} className="start-button ">
        start
      </button>
      <div className="game-caution">
        {caution_text}
      </div>
      <div className="credit">
        制作： トキハゲン　　　　　　キャラクター著作： 朝雲
      </div>
    </div>
    )
  }
}

