import React from 'react'
//icon
import Icon from '@mdi/react';
import {mdiClose} from '@mdi/js';

// components
import ItemCard from './item';
import PlayerCard from './PlayerCard';
import NameBar from './NameBar';
import MenuBar from './MenuBar';
import StartView from './StartView';

// images
  // character
  import stand from '../images/stand.png';
  import sit from '../images/sit.png';
  import runL from '../images/run_left.png';
  import runL2 from '../images/run_left2.png';
  import runR from '../images/run_right.png';
  import runR2 from '../images/run_right2.png';
  import love from '../images/love.png';

import './style/Game.css';

const chara_image = {
  "stand": stand,
  "sit": sit,
  "runL": runL,
  "runL2": runL2,
  "runR": runR,
  "runR2": runR2,
  "love": love
}

const window_width = window.innerWidth;
const window_height = window.innerHeight;
const chara_width = window_width * 0.5 >= 300 ? 300 : window_width * 0.5

//################## Game ##################
export default class Game extends React.Component{
  constructor(){
    super();
    this.canvas = null;
    this.dialog = null;
    this.ts = 0;
    this.cimg = new Image();
    this.cimg.src = chara_image.runR
    this.img_state = {"x": 100, "y": 0, "w": chara_width, "h": chara_width, "flip": false, count: 0};
    this.isHandMode = false;
    this.isDialog = false;
    this.itemCardTag = <ItemCard />
    this.moveCard = <div className="dialog-tmp">申し訳ない！<br/>移動コマンドは<br/>まだ作成できていません…</div>
    this.playerCard = null;
    this.exp = 0;
    this.state = {
      start: true,
      mode_text: "nomal mode",
      name: "ゆうせい",
      level: 1,
      playerName: "ひですけさん",
      dialogCard: '',
    }
  }
  render(){
    const startElement = <StartView 
     startClick={()=>{
      this.initCanvas();
      this.setState({start: false})
      console.log(this.state.playerName)
      this.playerCard = <PlayerCard name={this.state.playerName} />;
     }}
     playerName={this.state.playerName} setPlayerName={(e)=>{this.setPlayerName(e)}}/>

    let start = this.state.start ? 
      startElement :
      <div>
        <NameBar level={this.state.level} name={this.state.name} />
        <text className="mode-text mx-auto">{this.state.mode_text}</text>
        <MenuBar 
         handClick={()=>{this.handClick()}}
         itemClick={()=>{this.itemClick()}}
         moveClick={()=>{this.moveClick()}}
         playerClick={()=>{this.playerClick()}}
        />
      </div>
    return (
      <div>
        {start}
        <div className="room" >
          <canvas 
           id="chara"
           ref={(self) => {this.canvas = self}}
           className="character"></canvas>
        </div>
        <dialog ref={(self)=>{this.dialog = self}} className="dialog">
          <button onClick={()=>{this.dialog.close()}} className="dialog-close">
            <Icon path={mdiClose} className="dialog-close-icon" color="#aaa"/>
          </button>
          {this.state.dialogCard}
        </dialog>
      </div>
    );
  }
  setPlayerName(e){
    this.setState({playerName: e.target.value})
  }
  initCanvas(){
    this.canvas.width = window_width;
    this.canvas.height = this.img_state.h *1.3;
    this.renderCanvas();
    const able_touch = window.ontouchstart === null;
    this.canvas.addEventListener(able_touch?'touchstart':'mousedown', (e)=>{this.characterClickDown(e, able_touch)})
    this.canvas.addEventListener(able_touch?'touchend':'mouseup', ()=>{this.characterClickUp()})
  }
  renderCanvas(){
    // ctx.scale(0.5, 0.5)
    let ctx = this.canvas.getContext('2d');
    ctx.drawImage(this.cimg, this.img_state.x, this.img_state.y, this.img_state.w, this.img_state.h)
    this. ts = window.requestAnimationFrame((ts)=>this.loop(ts, ctx))
  }
  loop(ts, ctx){
    ctx.clearRect(this.img_state.x,0,window_width, window_height)
    // 右へ走る
    if(!this.img_state.flip){
      this.img_state.x +=1;
//      this.img_state.y -=0.1;
      this.img_state.count ++;
      // 足変更
      if(this.img_state.count == 20){
        this.cimg.src = chara_image.runR2
        this.img_state.y = 10;
      }else if(this.img_state.count == 40){
        this.cimg.src = chara_image.runR
        this.img_state.count = 0
        this.img_state.y = 0;
      }
      // 端越えたら反転
      if(this.img_state.x > window_width - this.img_state.w){
        this.img_state.flip = true
        this.cimg.src = chara_image.runL
        this.img_state.count = 0;
      }
    //左へ走る
    }else{
      this.img_state.x -=1;
//      this.img_state.y +=0.1;
      this.img_state.count++;
      // 足変更
      if(this.img_state.count == 20){
        this.cimg.src = chara_image.runL2
        this.img_state.y = 10;
      }else if(this.img_state.count == 40){
        this.cimg.src = chara_image.runL
        this.img_state.count = 0
        this.img_state.y = 0;
      }
      // 端越えたら反転
      if(this.img_state.x <= -this.img_state.w/5){
        this.img_state.flip = false
        this.cimg.src = chara_image.runR
      }
    }
    
    ctx.drawImage(this.cimg, this.img_state.x, this.img_state.y, this.img_state.w, this.img_state.h)
    this.ts = window.requestAnimationFrame((ts) => this.loop(ts, ctx));
  }
  drawChara(ts){
    const ctx = this.canvas.getContext('2d') 
    //this.cimg.src = chara_image.sit
    ctx.clearRect(this.img_state.x, 0, window_width, window_height)
    ctx.drawImage(this.cimg, this.img_state.x, this.img_state.y, this.img_state.w, this.img_state.h)
  }
  
  checkCharacterClick(e, touch){
    if(this.isHandMode){
      const clickX = touch ? e.changedTouches[0].pageX : e.clientX
      let clickY = touch ? e.changedTouches[0].pageY :e.clientY
      clickY -= e.target.getBoundingClientRect().top
      if( !(this.img_state.x < clickX && clickX < this.img_state.x + this.img_state.w 
            && this.img_state.y < clickY && clickY < this.img_state.y + this.img_state.w)){
        return false
      }
      return true
    }else{
      return false
    }
  }
  characterClickDown(e, touch){
      if(this.checkCharacterClick(e, touch)){
        this.cimg.src = chara_image.love
        this.drawChara()
        this.exp++;
      }
  }
  characterClickUp(){
    this.cimg.src = chara_image.sit
    this.drawChara()
    // level up
    if( this.state.level < 99 && this.exp > this.state.level * 5 + 10){
      this.setState({level: ++this.state.level})
      this.exp = 0;
    }
  }
  itemClick(){
    this.setState({dialogCard: this.itemCardTag})
    this.dialog.showModal()
  }
  handClick(){    
    window.cancelAnimationFrame(this.ts);
    if(!this.isHandMode){
      this.cimg.src = chara_image.sit
      this.cimg.onload = () => {this.ts = window.requestAnimationFrame((ts)=>this.drawChara(ts))}
    }else{
      this.cimg.src = this.img_state.flip ? chara_image.runL : chara_image.runR
      this.renderCanvas()
    }
    this.setState(!this.isHandMode ? {mode_text: "hand mode"}:{mode_text: "nomal mode"});
    this.isHandMode = !this.isHandMode
  }
  moveClick(){
    this.setState({dialogCard: this.moveCard})
    this.dialog.showModal()
  }
  playerClick(){
    console.log(this.state.playerName)
    this.setState({dialogCard: this.playerCard})
    this.dialog.showModal()
  }
}

