import React from 'react';

import Icon from '@mdi/react';
import {mdiAccount, mdiBagPersonal, mdiArrowLeftBold, mdiHandRight} from '@mdi/js';
import './style/MenuBar.css';
export default class MenuBar extends React.Component{
  render(){
    const iconColor="eef0ff"
    return(
      <div className="menu-bar">
        <button className="menu-item" onClick={()=>{this.props.itemClick()}}>
          <div><Icon path={mdiBagPersonal}   color={iconColor} className="icon-image"/></div>
          <div className="icon-text"><text>item</text></div>
        </button>
        <button className="menu-item" onClick={()=>{this.props.handClick()}}>
          <Icon path={mdiHandRight}     color={iconColor} className="icon-image"/>
          <div className="icon-text"><text>brush</text></div>
        </button>
        <button className="menu-item" onClick={()=>{this.props.moveClick()}}>
          <Icon path={mdiArrowLeftBold} color={iconColor} className="icon-image"/>
          <div className="icon-text"><text>move</text></div>
        </button>
        <button className="menu-item" onClick={()=>{this.props.playerClick()}}>
          <Icon path={mdiAccount}       color={iconColor} className="icon-image"/>
          <div className="icon-text"><text>player</text></div>
        </button>
      </div>
    )
  }
}