import React from 'react';
import './style/PlayerCard.css';

export default class PlayerCard extends React.Component{
  constructor(props){
    super(props);
    this.state = {
      discription: "ゆうせいくんが懐いてる人。ゆうせいの幼馴染。\nブラック企業勤め"
    }
  }
  render(){
    return(
    <div className="player-card">
      <div className="pc-name">
        <span className="pc-n-title">飼い主:</span>
        <span className="pc-n-text">{this.props.name}</span>
      </div>
      <div className="pc-discription">
        <div className="pc-d-title">自己紹介:</div>
        <div className="pc-d-text">{this.state.discription}</div>
      </div>
    </div>
    )
  }
}