import React from 'react';
import './style/NameBar.css';
export default class NameBar extends React.Component{

  render(){
    return(
      <div className="name-bar">
        <div className="name-container">{this.props.name}</div>
        <div className="level-circle">Lv. {this.props.level}</div>
      </div>
    );
  }
}