import React from 'react'
import './style/item.css'

// item icons
import tmp_img from '../images/icons/tmp_img.png'
import no_img from '../images/icons/no_img.png'
import orange from '../images/icons/orange.png'
import rice_ball from '../images/icons/rice_ball.png'

export default class ItemCard extends React.Component{
    constructor(){
        super()
        this.state = {
            detail_icon: tmp_img,
            detail_name: '',
            detail_des: '',
        }
    }
    componentDidMount(){
        let class_items = document.getElementsByClassName('items');
        class_items[0].style.maxHeight = window.innerHeight * 0.3 + "px"
    }
    bag_items=[
        {name: 'おにぎり', description:'お米を炊いて握ったおにぎり。\nのりのいい香り', icon: rice_ball},
        {name: 'みかん', description:'ゆうせいくんの大好物。\n消化はよくないので体調のいいときにね', icon: orange},
        {name: '', description:'', icon: no_img},
        {name: '', description:'', icon: no_img},
        {name: '', description:'', icon: no_img},
        {name: '', description:'', icon: no_img}
    ];
    render(){
        return(
            <div className="image-card">
                <div className="title">持ち物を見てみよう!</div>
                <div className="items">
                <button className="item" onClick={()=>{this.clickItemDetail(0)}}>
                    <div>
                        <div className="item-img"><img src={rice_ball}/></div>
                       <div className="item-name">{this.bag_items[0].name}</div>
                    </div>
                </button>
                <button className="item" onClick={()=>{this.clickItemDetail(1)}}>
                    <div>
                        <div className="item-img"><img src={orange}/></div>
                        <div className="item-name">{this.bag_items[1].name}</div>
                    </div>
                </button>
                <button className="item" onClick={()=>{this.clickItemDetail(2)}}>
                    <div>
                        <div className="item-img"><img src={tmp_img}/></div>
                       <div className="item-name">{this.bag_items[2].name}</div>
                    </div>
                </button>
                <button className="item" onClick={()=>{this.clickItemDetail(3)}}>
                    <div>
                        <div className="item-img"><img src={tmp_img}/></div>
                       <div className="item-name">{this.bag_items[3].name}</div>
                    </div>
                </button>
                <button className="item" onClick={()=>{this.clickItemDetail(4)}}>
                    <div>
                        <div className="item-img"><img src={tmp_img}/></div>
                       <div className="item-name">{this.bag_items[4].name}</div>
                    </div>
                </button>
                <button className="item" onClick={()=>{this.clickItemDetail(5)}}>
                    <div>
                        <div className="item-img"><img src={tmp_img}/></div>
                       <div className="item-name">{this.bag_items[5].name}</div>
                    </div>
                </button>
            </div>
            <div className="details">
              <img className="detail-icon" src={this.state.detail_icon} alt="No Image"></img>
                <div className="detail-main">
                  <div className="detail-name">{this.state.detail_name}</div>
                  <div className="detail-text">{this.state.detail_des}</div>
                </div>
            </div>
            <div className="present-button"><button onClick={()=>{this.clickPresent()}}>渡す</button></div>
        </div>
        )
    }
    clickItemDetail(index){
        this.setState({
            detail_name: this.bag_items[index].name,
            detail_des: this.bag_items[index].description,
            detail_icon: this.bag_items[index].icon,
        })
    }
    clickPresent(){
        if(this.state.detail_name == ''){
            this.setState({
                detail_name: '',
                detail_des: '先にアイテムを選択してください',
                detail_icon: tmp_img,})
        }else{
            this.setState({
                detail_name: '',
                detail_des: '本当はこれで渡せるんですけどねえ…\nまだその段階にない',
                detail_icon: tmp_img,})
        }
    }
} 